"use client";
import { getAllVideos } from "@/endpoints/apiEndpoints";
import { useEffect, useState } from "react";
import axios from "axios";

export default function HomePage() {
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    const getAllVideosData = async () => {
      try {
        setLoading(true);
        const response = await getAllVideos();
        setVideos(response.data.data.videos);
      } catch (error) {
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };
    getAllVideosData();
  }, []);

  console.log(videos);

  return (
    <div className="flex justify-center items-center min-h-screen">
      <h1 className="text-2xl text-white">This is the home page</h1>
      <Navbar />
      {loading && <p className="text-white text-lg">Loading...</p>}
      {error && <p className="text-lg text-red-500">{error}</p>}
      <div className="">
        <ul className="list-style-none">
          {videos.length > 0 &&
            videos.map((video) => <li key={video._id}>{video.title}</li>)}
        </ul>
      </div>
    </div>
  );
}
